    <div class="right personal-center" v-if="!normalRightShow">
      <img src="../../assets/images/icon_news.png" alt="" class="icon-news" @click="handleNews">
      <img src="../../assets/images/icon_setting.png" alt="" class="icon-setting" @click="handleSetting">
    </div>
  </div>
</template>

<script>
export default {
  name: 'loginTab',
  props: {
    rightText: {
      type: String,
      default: ''
    },
    normalRightShow: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
    }
  },
  methods: {
    goBack () {
      this.$router.back(-1)
