// import { smsLogin } from '@/api/user'
import { getLocalStorage, setLocalStorage, removeLocalStorage } from '@/assets/js/localStorage'

const user = {
  state: {
    idNum: '',
    // token: '',
    userInfo: getLocalStorage('userInfo'),
    token: getLocalStorage('token')
    // forgetPwdStep: sessionStorage.getItem('forgetPwdStep') ? sessionStorage.getItem('forgetPwdStep') : 1
  },
  getters: {
    token: state => state.token,
    userInfo: state => state.userInfo
  },
  mutations: {
    SET_IDNUM: (state, idNum) => {
      state.idNum = idNum
    },
    SET_USER_INFO: (state, info) => {
      state.userInfo = info
      setLocalStorage('userInfo', info)
    },
    SET_TOKEN: (state, token) => {
      state.token = token
      setLocalStorage('token', token, 3600)
    }
  },
  actions: {
    Logout ({ commit, state }) {
      return new Promise((resolve, reject) => {
        commit('SET_USER_INFO', '')
        commit('SET_TOKEN', '')
        removeLocalStorage('token')
        removeLocalStorage('userInfo')
        resolve()
      }).catch(error => {

      })
    }
  }
}

export default user
