const smsLogin = resolve => require(['@/components/login/smsLogin.vue'], resolve)
const register = resolve => require(['@/components/register/register.vue'], resolve)
const registerProtocal= resolve => require(['@/components/register/registerProtocal.vue'], resolve)
const pwdLogin = resolve => require(['@/components/login/pwdLogin.vue'], resolve)
const forgetPassword = resolve => require(['@/components/password/forgetPassword.vue'], resolve)
const mobileValid = resolve => require(['@/components/password/mobileValid'], resolve)
const identifyValid = resolve => require(['@/components/password/identifyValid'], resolve)
const resetPassword = resolve => require(['@/components/password/resetPassword'], resolve)
const registerSucceed = resolve => require(['@/components/register/registerSucceed.vue'], resolve)
const setPassword = resolve => require(['@/components/password/setPassword'], resolve)
const setPasswordSucceed = resolve => require(['@/components/password/setPasswordSucceed'], resolve)
const resetPasswrodSucceed = resolve => require(['@/components/password/resetPasswordSucceed.vue'], resolve)


// const registerMain = resolve => require(['@/components/register/TheRegisterMain.vue'], resolve)

//
// const resetPasswrodSucceed = resolve => require(['@/components/password/TheResetPasswordSucceed'], resolve)

// const setPasswordMain = resolve => require(['@/components/password/TheSetPasswordMain'], resolve)

export default [
  {
    path: '/smsLogin',
    name: 'SmsLogin',
    component: smsLogin,
    meta: {
      title: '短信登录'
    }
  },
  {
    path: '/pwdLogin',
    name: 'PwdLogin',
    component: pwdLogin,
    meta: {
      title: '密码登录'
    }
  },
  {
    path: '/register',
    component: register,
    meta: {
      title: '注册'
    }
  },
  {
    path: '/registerSucceed',
    component: registerSucceed,
    meta: {
      title: '注册成功'
    }
  },
  {
    path: '/registerProtocal',
    component: registerProtocal,
    meta: {
      title: '注册协议'
    }
  },
  {
    path: '/setPassword',
    name: 'SetPassword',
    component: setPassword,
    meta: {
      title: '设置密码'
    }
  },
  {
    path: '/setPasswordSucceed',
    name: 'SetPasswordSucceed',
    component: setPasswordSucceed,
    meta: {
      title: '设置密码成功'
    }
  },
  {
    path: '/forgetPassword',
    redirect: 'forgetPassword/mobileValid',
    name: 'ForgetPassword',
    component: forgetPassword,
    children: [
      {
        path: 'mobileValid',
        component: mobileValid,
        meta: {
          title: '忘记密码'
        }

      },
      {
        path: 'identifyValid',
        name: 'IdentifyValid',
        component: identifyValid,
        meta: {
          title: '忘记密码'
        }
      },
      {
        path: 'resetPassword',
        name: 'ResetPassword',
        component: resetPassword,
        meta: {
          title: '重置密码'
        }
      },
      {
        path: 'resetPasswrodSucceed',
        name: 'ResetPasswrodSucceed',
        component: resetPasswrodSucceed,
        meta: {
          title: '重置密码成功'
        }
      }
    ]
  }
]
