import { axiosGet, axiosPost } from '../request'

export const getGroupInsuranceList = (params) =>
  axiosGet({
    config: { url: '/member/groupInsurance/list', params }
  })

export const getGroupInsuranceDetail = (params) =>
  axiosGet({
    config: { url: '/member/groupInsurance/detail', params }
  })

// 首页订单列表
export const getIndexOrderList = (params) =>
  axiosGet({
    config: { url: '/member/orders/getOrdersList', params }
  })

// 常用被保险人
export const getInsured = (params) =>
  axiosGet({
    config: { url: '/member/relation/getRelationList', params }
  })

// 删除常用被保险人
export const deleteInsured = (params) =>
  axiosGet({
    config: { url: '/member/relation/relationDelete', params }
  })

/* 添加常用被保险人 */
export const addInsured = (params) =>
  axiosGet({
    config: { url: '/member/relation/relationSave', params }
  })
// 获取收藏列表
export const getCollectList = (params) =>
  axiosGet({
    config: { url: '/member/favorite/getProductFavoriteList', params }
  })

/* 收藏与取消收藏 */
export const collectOrNot = (params) =>
  axiosGet({
    config: { url: '/member/favorite/doFavorite', params }
  })

/* 订单详情 */
export const orderDetail = (params) =>
  axiosGet({
    config: { url: '/member/orders/ordersDetail', params }
  })

/* 常用被保险人关系类型 */
export const insuredType = (params) =>
  axiosGet({
    config: { url: '/member/relation/getRelationalType', params }
  })
