import Vue from 'vue'
import Router from 'vue-router'

import loginRegister from './login-register'
import product from './product'
import personalCenter from './personalCenter'

Vue.use(Router)

export default new Router({
  scrollBehavior: (to, from, savedPosition) => savedPosition || { x: 0, y: 0 },
  // mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'Home',
      component: resolve => require(['@/views/Home.vue'], resolve),
      meta: {
        title: '首页',
        requireAuth: false,
        isFoot: true
      }
    },
    {
      path: '/product',
      name: 'Product',
      redirect: '/product/list',
      component: resolve => require(['@/views/ProductList.vue'], resolve),
      meta: {
        title: '产品列表页',
        requireAuth: false,
        isFoot: true
      },
      children: [
        ...product
      ]
    },
    {
      path: '/personalCenter',
      name: 'PersonalCenter',
      component: resolve => require(['@/views/PersonalCenter.vue'], resolve),
      meta: {
        title: '个人中心',
        requireAuth: false,
        isFoot: true
      }
    },
    {
      path: '/smsLogin',
      name: 'SmsLogin',
      component: resolve => require(['@/components/login/smsLogin.vue'], resolve),
      meta: {
        title: '短信登录'
      }
    },
    ...loginRegister,
    ...personalCenter,
    {
      path: '*',
      component: resolve => require(['@/views/NotFound.vue'], resolve),
      meta: {
        title: '功能开发中...'
      }

    }
  ]
})
