import { setLocalStorage } from '@/assets/js/localStorage'

const mutations = {
  SET_DETAIL_TAB_CUR: (state, index) => {
    state.detailTabCur = index
    setLocalStorage('detailTabCur', index)
  },
  SET_ORDER_TAB_CUR: (state, value) => {
    state.orderTabCur = value
    setLocalStorage('orderTabCur', value)
  },
  SET_ORDER_TAB_CUR_STATE: (state, value) => {
    state.orderTabCurState = value
    setLocalStorage('orderTabCurState', value)
  }
}

export default mutations
