import { axiosGet } from '../request'

/* 产品详情页 */
export const productDetail = (params) =>
  axiosGet({
    config: { url: '/shop/product/detail', params }
  })

/* 获取计划及计划详情 */
export const getPlans = (params) =>
  axiosGet({
    config: { url: '/shop/product/getPlans', params }
  })

/* 获取测算因子 */
